import React from "react";

function App() {
  const [, updateState] = React.useState();
  const forceUpdate = React.useCallback(() => updateState({}), []);
  const [round, setRound] = React.useState(0);
  const [userSet, setUserSet] = React.useState([0, 0, 0, 0, 0]);
  const [npcSet, setNpcSet] = React.useState([0, 0, 0, 0, 0]);
  const [rerollSet, setRerollSet] = React.useState([]);
  const [status, setStatus] = React.useState("");
  const getCount = set => {
    var count = {};
    set.forEach(function(x) {
      count[x] = (count[x] || 0) + 1;
    });

    return [...new Array(6)].map((item, index) => ({
      value: index + 1,
      occurances: count[index + 1] || 0
    }));
  };
  const getResult = set => {
    let counts = getCount(set);
    let resValue = { value: 1, name: "Nic" };
    let firstPair = counts.findIndex(item => item.occurances > 1);
    let secondPair =
      firstPair !== -1
        ? counts.findIndex(
            item =>
              item.value !== counts[firstPair].value && item.occurances > 1
          )
        : -1;

    if (firstPair === -1) resValue = { value: 5, name: "Mały Strit" };
    if (
      firstPair === -1 &&
      !counts.find(item => item.value === 1 && item.occurances > 0)
    )
      resValue = { value: 6, name: "Duży Strit" };
    if (firstPair !== -1) {
      resValue = { value: 2, name: "Para" };
      if (counts[firstPair].occurances === 3)
        resValue = { value: 4, name: "Trójka" };
      if (counts[firstPair].occurances === 4)
        resValue = { value: 8, name: "Kareta" };
      if (counts[firstPair].occurances === 5)
        resValue = { value: 9, name: "Poker" };
      if (secondPair !== -1) {
        resValue = { value: 3, name: "Dwie pary" };
        if (counts[firstPair].occurances === 3 && secondPair !== -1)
          resValue = { value: 7, name: "Full" };
        if (
          secondPair !== -1 &&
          counts[secondPair].occurances === 3 &&
          firstPair !== -1
        )
          resValue = { value: 7, name: "Full" };
      }
    }

    return resValue;
  };

  const rollD6 = () => {
    return Math.floor(6 * Math.random()) + 1;
  };
  const rollSet = (set, array) => {
    let newSet = set;

    set.forEach((item, index) => {
      if (item === 0 || array.includes(index)) {
        newSet[index] = rollD6();
      }
    });
    return newSet;
  };

  const getNpcReroll = () => {
    let userPoints = getResult(userSet).value;
    let npcPoints = getResult(npcSet).value;
    if (
      (round >= 2 && getResult(userSet).value === getResult(npcSet).value) ||
      (round >= 1 && userPoints >= npcPoints) ||
      rerollSet.length > 0
    ) {
      var count = {};
      npcSet.forEach(function(x) {
        count[x] = (count[x] || 0) + 1;
      });

      var newRerollSet = [];
      Object.keys(count)
        .map(key => ({ value: key, occurances: count[key] }))
        .forEach((item, index) => {
          if (item.occurances === 1) {
            newRerollSet.push(
              npcSet.findIndex(roll => {
                return parseInt(roll) === parseInt(item.value);
              })
            );
          }
        });

      return newRerollSet;
    }
    return [];
  };

  const confirmMove = () => {
    setStatus("inprogress");
    setUserSet(rollSet(userSet, rerollSet));
    setNpcSet(rollSet(npcSet, getNpcReroll()));
    setRound(round + 1);
    setRerollSet([]);
    forceUpdate();
  };

  const getPointsFromSet = set => {
    var count = {};
    set.forEach(function(x) {
      count[x] = (count[x] || 0) + 1;
    });

    var points = 0;
    Object.keys(count)
      .map(key => ({ value: key, occurances: count[key] }))
      .forEach((item, i) => {
        if (item.occurances > 1) {
          points = points + item.value * item.occurances;
        }
      });

    return points;
  };
  const getWinner = walkover => {
    if (walkover === true) {
      return "failure";
    } else {
      let userPoints = getResult(userSet).value;
      let npcPoints = getResult(npcSet).value;

      if (userPoints > npcPoints) return "victory";
      if (userPoints < npcPoints) return "failure";
      if (userPoints === npcPoints) {
        let reducedUserSet = userSet.reduce((a, b) => {
          return a + b;
        });
        let reducedNpcSet = npcSet.reduce((a, b) => {
          return a + b;
        });
        if (reducedUserSet > reducedNpcSet) return "victory";
        if (reducedUserSet < reducedNpcSet) return "failure";
        if (reducedUserSet === reducedNpcSet) return "draw";
      }
    }
  };
  const finishGame = walkover => {
    status === "" || status === "inprogress"
      ? setStatus(
          getWinner(
            getPointsFromSet(userSet),
            getPointsFromSet(npcSet),
            walkover
          )
        )
      : restartGame();
  };
  const restartGame = () => {
    setUserSet([0, 0, 0, 0, 0]);
    setNpcSet([0, 0, 0, 0, 0]);
    setRerollSet([]);
    setRound(0);
    setStatus("");
    forceUpdate();
  };
  return (
    <div>
      <div
        style={{
          display: "flex",
          padding: 12,
          alignItems: "center",
          justifyContent: "center",
          flexDirection: "column",
          textAlign: "center"
        }}
      >
        {status === "inprogress" && (
          <div style={{ color: "gray" }}>
            Kliknij na kość żeby oznaczyć ją do przerzucenia
          </div>
        )}
        <br />
        <br />
        Twoje kości
        <div style={{ display: "flex" }}>
          {userSet.map((item, index) => (
            <div>
              <img
                style={{
                  padding: 3,
                  cursor: "pointer",
                  filter:
                    rerollSet.findIndex(reroll => reroll === index) !== -1 &&
                    "sepia(100%) hue-rotate(90deg) saturate(400%) brightness(80%)"
                }}
                alt={item}
                height="30"
                width="30"
                src={`./images/dice${item}.png`}
                onClick={() => {
                  if (status === "inprogress") {
                    let rerollIndex = rerollSet.findIndex(
                      reroll => reroll === index
                    );
                    if (rerollIndex !== -1) {
                      let newReroll = rerollSet;
                      newReroll.splice(rerollIndex, 1);
                      setRerollSet(newReroll);
                    } else {
                      let newReroll = rerollSet;
                      newReroll.push(index);
                      setRerollSet(newReroll);
                    }
                    forceUpdate();
                  }
                }}
              />
            </div>
          ))}
        </div>
        {round !== 0 && `${getResult(userSet).name}`}
      </div>
      <div
        style={{
          display: "flex",
          padding: 12,
          alignItems: "center",
          justifyContent: "center",
          flexDirection: "column"
        }}
      >
        Kości przeciwnika
        <div style={{ display: "flex" }}>
          {npcSet.map(item => (
            <div style={{ filter: "brightness(60%)" }}>
              <img
                style={{ padding: 3 }}
                alt={item}
                height="30"
                width="30"
                src={`./images/dice${item}.png`}
              />
            </div>
          ))}
        </div>
        {round !== 0 && `${getResult(npcSet).name}`}
      </div>
      <div
        style={{
          display: "flex",
          padding: 12,
          alignItems: "center",
          justifyContent: "center"
        }}
      >
        <button
          style={{ margin: 12 }}
          onClick={()=>
            round >= 2 && getResult(userSet).value !== getResult(npcSet).value
              ? finishGame(false)
              : confirmMove()
          }
        >
          {round === 0 && "ROZPOCZNIJ GRĘ"}
          {round === 1 &&
            (rerollSet.length > 0
              ? "PRZERZUĆ KOŚCI I KONTYNUUJ"
              : "KONTYNUUJ BEZ PRZERZUCANIA")}
          {round >= 2 &&
            getResult(userSet).value !== getResult(npcSet).value &&
            (status === "inprogress" ? "ZAKOŃCZ GRĘ" : "ZACZNIJ OD NOWA")}
          {round >= 2 &&
            getResult(userSet).value === getResult(npcSet).value &&
            (rerollSet.length > 0
              ? "PRZERZUĆ KOŚCI I KONTYNUUJ"
              : "KONTYNUUJ BEZ PRZERZUCANIA")}
        </button>

        <button
          disabled={
            round === 0 ||
            (round >= 2 && getResult(userSet).value !== getResult(npcSet).value)
          }
          style={{ margin: 12 }}
          onClick={() => finishGame(true)}
        >
          PODDAJ SIĘ
        </button>
      </div>
      <div
        style={{
          display: "flex",
          padding: 12,
          alignItems: "center",
          justifyContent: "center",
          flexDirection: "column"
        }}
      >
        <h3
          style={{
            color:
              status === "victory"
                ? "green"
                : status === "failure"
                ? "red"
                : status === "draw" && "gray"
          }}
        >
          {status === "victory" && "ZWYCIĘSTWO!"}
          {status === "failure" && "PORAŻKA!"}
          {status === "draw" && "REMIS!"}
          {status === "inprogress" && "Runda " + parseInt(round)}
        </h3>
        <button onClick={restartGame}>Resetuj</button>
      </div>
    </div>
  );
}

export default App;
